body {
  background: url(../../presale_assets/background-bg.png) top no-repeat #011145;
  font-family: Poppins, sans-serif;
  margin: 0;
  padding: 0;
}
.main-bg-con {
  background: url(../../presale_assets/footer-bg.png) bottom no-repeat;
  display: inline-block;
  width: 100%;
}
* {
  box-sizing: border-box;
}
.container-con {
  margin: 0 auto;
  width: 1120px;
}
.main-width {
  display: inline-block;
  width: 100%;
}
.header-section {
  padding: 35px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-section .header-right-buttons {
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  padding-top: 7px;
  text-align: right;
  text-transform: uppercase;
}
.header-section .header-right-buttons a {
  color: #fff;
  text-decoration: none;
}
.connect-wallet-button {
  background: #42b51f;
  background: linear-gradient(180deg, #42b51f, #049702);
  border-radius: 50px;
  display: inline-block;
  padding: 13px 32px;
}
.banner-section {
  display: flex;
  justify-content: space-between;
}
.banner-left-con {
  flex: 0 0 690px;
}
.banner-img-con img {
  display: inline-block;
}
.banner-left-con h1 {
  color: #fff;
  display: inline-block;
  font-size: 36px;
  font-style: italic;
  font-weight: 800;
  line-height: 40px;
  margin: 0;
  padding: 52px 0 37px;
  text-transform: uppercase;
  width: 100%;
}
.banner-left-con h1 span {
  display: inline-block;
  font-size: 24px;
  font-weight: 600;
  line-height: 24px;
  width: 100%;
}
.banner-left-con p {
  color: #b7bfc7;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 400;
  padding-bottom: 15px;
}
.whitepaper-btn {
  background: #1f2f5c;
  border: 2px solid #b4b9c8;
  border-radius: 50px;
  color: #fff;
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  line-height: 14px;
  padding: 13px 34px;
  text-decoration: none;
  text-transform: uppercase;
}
.banner-left-bottom-con {
  padding: 1.5rem 0;
  padding-bottom: 2rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.banner-left-bottom-con img {
  margin-left: 34px;
  margin-right: 20px;
  margin-top: 1px;
}
.banner-bottom-img-1 {
  position: relative;
}
.banner-bottom-img-1:after {
  background-image: url(../../presale_assets/banner-bottom-devider.png);
  background-position: 0 0;
  content: "";
  height: 69px;
  position: absolute;
  right: -9px;
  top: -10px;
  width: 1px;
}
.banner-right-con {
  flex: 0 0 370px;
}
.banner-right-con h2 {
  background: url(../../presale_assets/banner-right-heading-bg.png) 0 0
    no-repeat;
  color: #fff;
  display: inline-block;
  font-size: 36px;
  font-style: italic;
  font-weight: 800;
  margin: 0;
  padding: 3px 0;
  text-align: center;
  width: 100%;
}
.countdown-con {
  background: #fff;
  padding-bottom: 40px;
}
.countdown-con h3 {
  color: #011145;
  padding: 13px 0;
  font-family: Inter, sans-serif;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}
.pre-sale-info {
  background: #fff;
  margin-top: 4px;
  padding: 0 20px 20px;
  position: relative;
}
.pre-sale-info:after {
  left: 10px;
}
.pre-sale-info:after,
.pre-sale-info:before {
  background-image: url(../../presale_assets/right-section-handle.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  content: "";
  height: 52px;
  position: absolute;
  top: -28px;
  width: 15px;
}
.pre-sale-info:before {
  right: 10px;
}
.pre-sale-info h3 {
  color: #011145;
  display: flex;
  padding: 13px 0;
  justify-content: center;
  align-items: center;
  font-family: Inter, sans-serif;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}
.pre-sale-info-inner-con {
  background: url(../../presale_assets/right-banner-bg.png) center -20px no-repeat;
  border: 2px solid #d9dfe9;
}
.presale-price {
  border-bottom: 2px solid #d9dfe9;
  color: #011145;
  display: inline-block;
  font-family: Inter, sans-serif;
  font-size: 28px;
  line-height: 28px;
  font-weight: 500;
  padding: 15px 0;
  text-align: center;
  width: 100%;
}
.presale-price span {
  color: #627596;
  display: inline-block;
  font-size: 12px;
  margin-top: 0;
  width: 100%;
}
.min-max-purchase {
  display: flex;
}
.min-max-purchase div {
  color: #011145;
  font-family: Inter, sans-serif;
  font-size: 28px;
  line-height: 28px;
  font-weight: 500;
  padding: 15px 0;
  text-align: center;
  flex: 0 0 50%;
}
.min-max-purchase div span {
  color: #627596;
  display: inline-block;
  font-size: 12px;
  margin-top: 0;
  width: 100%;
}
.min-purchase {
  border-right: 2px solid #d9dfe9;
}
.how-to-buy-con {
  display: flex;
  margin-top: 20px;
  margin-bottom: 60px;
}
.how-to-buy-box {
  background: #fff;
  padding: 25px 45px 25px;
  flex: 0 0 370px;
}
.how-to-buy-box h3 {
  color: #011145;
  display: flex;
  align-items: center;
  font-family: Inter, sans-serif;
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 12px;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}
.how-to-buy-box h3 img {
  display: inline-block;
  margin-left: -13px;
  margin-top: 0px;
}
.buy-ibat-con {
  background: #fff url(../../presale_assets/ibat-bg-img.jpg) 100% 0 no-repeat;
}
.buy-ibat-con h3 {
  justify-content: center;
}
.buy-ibat-con img {
  margin-top: -19px !important;
}
.how-to-buy-box p {
  color: #011145;
  display: flex;
  align-items: center;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin: 0;
  padding: 0 0 15px;
  width: 100%;
}
.how-to-buy-box p span {
  border: 1px solid #011145;
  height: 20px;
  line-height: 19px;
  margin-right: 10px;
  flex: 0 0 20px;
}
.how-to-buy-box .btn,
.how-to-buy-box p span {
  border-radius: 50px;
  text-align: center;
}
.how-to-buy-box .btn {
  background: #42b51f;
  background: linear-gradient(180deg, #42b51f, #049702);
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 13px 0;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
}
.buy-ibat-btn {
  background: #fd0101 !important;
  background: linear-gradient(180deg, #fd0101, #df1f21) !important;
}
.buy-ibat-con h3 {
  margin-bottom: 30px !important;
}
a.btn.buy-ibat-btn {
  margin-bottom: 35px;
}
.add-refer-btn {
  background: #004090 !important;
  background: linear-gradient(180deg, #004090, #1b1275) !important;
}
.how-to-buy-box label {
  color: #011145;
  margin-top: 10px;
  padding-bottom: 6px;
}
.how-to-buy-box input,
.how-to-buy-box label {
  display: inline-block;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
  width: 100%;
}
.how-to-buy-box input {
  background: #fff;
  border: 2px solid #627597;
  border-radius: 50px;
  box-shadow: 0 6px 14px -7px rgba(0, 0, 0, 0.43);
  color: #627596;
  height: 36px;
  margin-bottom: 10px;
  padding: 0 20px;
}
.how-to-buy-box input::-moz-placeholder {
  color: #627596;
  opacity: 1;
}
.how-to-buy-box input:-ms-input-placeholder {
  opacity: 1;
}
.how-to-buy-box input::placeholder {
  color: #627596;
  opacity: 1;
}
.how-to-buy-box input:-ms-input-placeholder {
  color: #627596;
}
.how-to-buy-box input::-ms-input-placeholder {
  color: #627596;
}
.buy-ibat-con {
  margin: 0 5px;
}
.refer-con {
  color: #011145;
  display: flex;
  justify-content: space-between;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin-top: 20px;
  text-align: center;
  width: 100%;
}
.refer-con div {
  flex: 0 0 50%;
}
.refer-con span {
  border: 2px solid #d9dfe9;
  color: #011145;
  display: inline-block;
  font-family: Inter, sans-serif;
  font-size: 22px;
  font-weight: 500;
  margin-top: 10px;
  padding: 7px 5px;
  width: 95%;
}
.vesting-main-con {
  background: #fff;
  margin: 50px 0;
  padding: 20px 22px 30px;
}
.visting-inner-con {
  width: 100%;
  display: flex;
}
.vesting-main-con h2 {
  color: #011145;
  display: inline-block;
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
  padding-bottom: 20px;
}
.vesting-box {
  margin: 0 5px;
  width: 100%;
}
.vesting-box h3 {
  background: #00418f;
  color: #fff;
  display: inline-block;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 700;
  margin: 0;
  padding: 5px 0;
  text-align: center;
  width: 100%;
}
.vesting-box ul {
  border: 2px solid #d9dfe9;
  border-top: 0 !important;
  display: inline-block;
  list-style: none;
  margin: 0;
  min-height: 100px;
  padding: 10px 0 0;
  width: 100%;
}
.vesting-box ul li {
  display: inline-block;
  color: #011145;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 5px;
  text-align: center;
  width: 100%;
}
.as-seen-on-con {
  text-align: center;
}
.as-seen-on-con h2 {
  background: url(../../presale_assets/as-seen-on-bg.png) top no-repeat;
  color: #fff;
  display: inline-block;
  font-size: 36px;
  font-style: italic;
  font-weight: 800;
  margin: 0 0 30px;
  padding: 3px 0;
  text-align: center;
  width: 100%;
}
.as-seen-img-con {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.as-seen-on-con img {
  display: inline-block;
  height: 100%;
  margin: 0 25px 25px;
}
.meet-team-con {
  padding-top: 20px;
}
.meet-team-inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.meet-team-con .team-box {
  color: #fff !important;
  margin-bottom: 20px;
  text-align: center;
  flex: 0 0 24%;
}
.team-box a {
  display: inline-block;
}
.meet-team-con .team-box img {
  margin: 0;
  width: 90%;
  height: 100%;
}
.meet-team-con .team-box > img {
  height: auto;
}
.footer-con ul {
  display: flex;
  justify-content: center;
  list-style: none;
  margin-top: 200px;
  padding: 0;
  width: 100%;
}
.footer-con ul li {
  padding: 0 10px;
}
.footer-con ul li a {
  color: #fff;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
}
.footer-social-icon {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  width: 100%;
}
.footer-social-icon a {
  display: inline-block;
  padding: 0 5px;
}
.copyright {
  color: #60646f;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 400;
  margin-bottom: 60px;
  margin-top: 20px;
  text-align: center;
}
p#timer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
p#timer div {
  flex: 0 0 72px;
  height: 72px;
  background: url(../../presale_assets/timer-bg.png) left top no-repeat;
  text-align: center;
  margin-right: 10px;
  font-size: 28px;
  line-height: 35px;
  color: #fff;
  font-weight: 700;
  font-family: "Inter", sans-serif;
  padding-top: 10px;
  position: relative;
}
p#timer div::after {
  content: ":";
  position: absolute;
  right: -9px;
  top: 16px;
  color: #737f8b;
  font-size: 22px;
}
p#timer div:last-child::after {
  display: none;
}
p#timer div:last-child {
  margin: 0;
}
p#timer div span {
  display: block;
  font-size: 12px;
  line-height: 12px;
  text-transform: uppercase;
  width: 100%;
}
.copy-outer {
  display: inline-block;
  position: relative;
  width: 100%;
}
.copy-button {
  background: #004090;
  background: linear-gradient(180deg, #004090, #1b1275);
  border: 0;
  border-radius: 50px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  height: 36px;
  letter-spacing: 1px;
  line-height: 38px;
  padding: 0 19px;
  position: absolute;
  right: 0;
  text-transform: uppercase;
  top: -18px;
}
.tooltip-code {
  display: block;
  position: relative;
  margin-top: -28px;
}
.tooltip-code .tooltiptext {
  background-color: #1f2f5c;
  border-radius: 6px;
  bottom: 150%;
  color: #fff;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 400;
  left: 50%;
  line-height: 15px;
  margin-left: -75px;
  opacity: 0;
  padding: 5px;
  position: absolute;
  text-align: center;
  text-transform: none;
  transition: opacity 0.3s;
  visibility: hidden;
  width: 140px;
  z-index: 1;
}
.tooltip-code .tooltiptext:after {
  border: 5px solid transparent;
  border-top-color: #1f2f5c;
  content: "";
  left: 50%;
  margin-left: -5px;
  position: absolute;
  top: 100%;
}
.tooltip-code:hover .tooltiptext {
  opacity: 1;
  visibility: visible;
}
label#web3-login-mm {
  background: #004090 !important;
  background: linear-gradient(180deg, #004090, #1b1275) !important;
  border: 0;
  color: #fff;
  font-size: 18px;
}
label#web3-login-wc {
  background: #fd0101 !important;
  background: linear-gradient(180deg, #fd0101, #df1f21) !important;
  border: 0;
  color: #fff;
  font-size: 18px;
}
